"use client";

export default function DashboardPage() {
  return (
    <div className="min-h-screen bg-sky-200 flex flex-col items-center justify-center p-6">
      <div className="absolute top-4 left-6 flex items-center gap-2">
        <img src="/favicon.ico" alt="App Icon" className="w-6 h-6" />
        <span className="font-bold text-lg text-blue-700">Task Manager</span>
      </div>

      <h1 className="text-3xl font-bold text-center text-blue-800 mb-6">Dashboard</h1>

      <div className="flex gap-4">
        <button
          onClick={() => (window.location.href = "/alltasks")}
          className="bg-blue-600 text-white px-6 py-3 rounded shadow-md hover:bg-blue-700"
        >
          Go to Tasks
        </button>
      </div>
    </div>
  );
}
